/* table css start */
.custom--table {
  thead {
    @extend %base-color-two;
    th {
      border-bottom: 1px solid #f0f1f5 !important;
      border-top: none;
      padding: rem(12px) rem(20px);
      color: #fff;
      font-size: 12px;
      text-transform: uppercase;
      text-align: center;
      font-weight: 600;
      vertical-align: middle;
      &:first-child {
        text-align: left;
        @include border-radius(8px 0 0 0);
 }        // padding-left: 0
      &:last-child {
        text-align: right;
        @include border-radius(0 8px 0 0);
 } } }        // padding-right: 0
  tbody {
    td {
      border-top: none;
      border-bottom: 1px solid $border-color;
      padding: 15px 20px;
      color: $para-color;
      text-align: center;
      vertical-align: middle;
      &:first-child {
        text-align: left; }
      &:last-child {
        text-align: right; } }
    tr {
      &:last-child {
        td {
          border-bottom: none; } } } } }
.t-company-thumb {
  width: 55px;
  height: 55px;
  @extend %bg--white;
  @extend %bs-5;
  overflow: hidden;
  box-shadow: 0 3px 15px rgba(#000, 0.3); }
.t-company-content {
  padding-left: rem(15px); }
.t-rating {
  img {
    max-height: 25px; } }
// table responsive
[data-label] {
  position: relative;
  &::before {
    position: absolute;
    content: attr(data-label);
    font-weight: 700;
    color: #000000;
    top: 0;
    left: 0;
    padding: rem(13px) rem(15px);
    display: none;
    font-size: rem(12px); } }
.table-responsive--md {
  @media (max-width: 991px) {
    thead {
      display: none; }
    tbody {
      tr {
        &:nth-child(odd) {
          background-color: #f7f7f7; }
        td {
          padding-right: 15px;
          &:last-child {
            padding-right: 15px; } } } }
    tr {
      th, td {
        display: block;
        padding-left: 45%!important;
        text-align: right!important;
        border-top: 1px solid rgba(#000000, 0.08) !important;
        &:first-child {
          border-top: none !important; } } }
    [data-label] {
      &::before {
        display: block; } } } }
.table-responsive--sm {
  @media (max-width: 767px) {
    thead {
      display: none; }
    tbody {
      tr {
        &:nth-child(odd) {
          background-color: f7f7f7; }
        td {
          padding-right: 15px;
          &:last-child {
            padding-right: 15px; } } } }
    tr {
      th, td {
        display: block;
        padding-left: 45%!important;
        text-align: right!important;
        border-top: 1px solid rgba(#000000, 0.08) !important;
        &:first-child {
          border-top: none !important; } } }
    [data-label] {
      &::before {
        display: block; } } } }
*[class*="table-responsive--"] {
  &.data-label--none {
    @media (max-width: 1199px) {
      tr {
        th, td {
          padding-left: .75rem; } } } } }
/* table css end */
