/* sidebar css start */
.sidebar-widget {
  background-color: #ffb88021;
  border: 1px solid $border-color;
  @extend %bs-8;
  overflow: hidden;
  &__header {
    padding: rem(12px) rem(20px);
    border-bottom: 1px solid $border-color;
    // background-color: #FBF2D4
    @extend %base-color-two; }
  &__title {
    font-size: rem(18px);
    color: #fff; }
  &__body {
    padding: rem(15px) rem(20px); } }
/* sidebar css end */

/* action-sidebar css start */
.action-sidebar {
  padding: rem(20px);
  background-color: #fff;
  @extend %transition;
  @include media(991px) {
    padding: 30px;
    width: 350px;
    position: fixed;
    top: 100px;
    left: -105%;
    z-index: 99999;
    max-width: 100%;
    box-shadow: 5px 5px 10px rgba(#000, 0.15);
    max-height: calc(100vh - 75px);
    overflow: auto;
    scrollbar-width: thin;
    scrollbar-color: darkgrey #e7e7e7;
    &::-webkit-scrollbar {
      width: 5px; }
    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
    &::-webkit-scrollbar-thumb {
      background-color: darkgrey;
      @include border-radius(999px); }
    &.active {
      left: 0; } }
  @include media(420px) {
    width: 100%; } }
.action-widget {
  padding: 20px 0;
  border-bottom: 1px solid lighten($border-color , 5%);
  &:last-child {
    padding-bottom: 0;
    border-bottom: none; }
  &__title {
    font-size: rem(15px);
    color: $heading-color;
    // padding-bottom: rem(5px)
    // border-bottom: 1px solid lighten($border-color, 8%)
    position: relative;
    padding-right: 20px;
    cursor: pointer;
    &::after {
      position: absolute;
      content: "\f107";
      font-family: 'Line Awesome Free';
      font-weight: 900;
      top: 0;
      right: 0;
      font-size: rem(14px); } }
  &__body {
    margin-top: rem(15px);
    &.scroll--active {
      max-height: 200px;
      overflow: auto;
      scrollbar-width: thin;
      scrollbar-color: darkgrey #e7e7e7;
      &::-webkit-scrollbar {
        width: 5px; }
      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
      &::-webkit-scrollbar-thumb {
        background-color: darkgrey;
        @include border-radius(999px); } } } }
.search-form-inline {
  position: relative;
  input {
    padding-left: 10px;
    padding-right: 30px; }
  &__btn {
    position: absolute;
    top: 50%;
    right: 5px;
    @include transform(translateY(-50%));
    background-color: transparent; } }
.action-widget {
  .form-check + .form-check {
    margin-top: rem(5px); } }

.sidebar-category {
  margin: rem(-3px) 0;
  &__single {
    padding: rem(3px) 0;
    a {
      font-size: rem(14px);
      color: $para-color;
      &:hover {
        @extend %text--base; } }
    .value {
      &::before {
        content: '('; }
      &::after {
        content: ')'; } } }
  .more {
    cursor: pointer;
    @extend %text--base; } }

.action-sidebar-close {
  width: 30px;
  height: 30px;
  background-color: $base-color;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  display: none;
  @include media(991px) {
    display: inline-block; } }
.action-sidebar-open {
  padding: rem(8px) rem(25px);
  background-color: $base-color;
  color: #fff;
  align-items: center;
  justify-content: center;
  @include border-radius(3px);
  display: none;
  @include media(991px) {
    display: inline-flex; }
  @include media(420px) {
    width: 100%; }
  i {
    font-size: rem(22px);
    margin-right: 5px; } }
/* action-sidebar css end */
