/* profile header section css start */
.profile-header {
  padding: rem(80px) 0; }

.donor-profile {
  @extend %d-flex;
  align-items: center;
  @include media(440px) {
    justify-content: center;
    text-align: center; }
  &__thumb {
    width: 120px;
    @extend %bs-8;
    overflow: hidden; }
  &__content {
    width: calc(100% - 120px);
    padding-left: rem(25px);
    @include media(440px) {
      width: 100%;
      padding-left: 0;
      margin-top: rem(20px); }
    p {
      color: #fff; } }
  &__name {
    color: #fff; } }

.dono-info-item {
  @include media(440px) {
    justify-content: center; } }

/* profile header section css end */
