/* hero section css start */
.hero {
  padding: rem(120px) 0;
  position: relative;
  z-index: 1;
  @include media(991px) {
    padding: rem(90px) 0; }
  &::before {
    @include positionTwo;
    background: #bdc3c7;
    background: -webkit-linear-gradient(to top, rgba($base-color-two, 0.98), rgba($base-color-two, 0.45));
    background: linear-gradient(to top, rgba($base-color-two, 0.98), rgba($base-color-two, 0.45));
    z-index: -1; }
  &__title {
    font-size: rem(52px);
    @extend %text--white;
    @include media(1399px) {
      font-size: rem(48px); }
    @include media(991px) {
      font-size: rem(42px); }
    @include media(575px) {
      font-size: rem(36px); } }
  &__blood-search-form {
    margin-top: rem(30px);
    @extend %d-flex;
    padding: rem(10px);
    @extend %bg--white;
    @extend %bs-8;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    &::before {
      position: absolute;
      content: '';
      top: -10px;
      left: -10px;
      width: calc(100% + 20px);
      height: calc(100% + 20px);
      @extend %bg--white;
      opacity: 0.15;
      z-index: -1;
      @extend %bs-8; }
    .input-field {
      position: relative;
      width: calc((100% /2) - 70px);
      @include media(520px) {
        width: calc((100% / 2) - 10px); }
      @include media(420px) {
        width: 100%; }
      input, select {
        background-color: transparent;
        border: none;
        border-bottom: 1px solid $border-color;
        height: 50px;
        padding: 15px 10px;
        padding-left: rem(30px);
        width: 100%;
        @include media(767px) {
          font-size: rem(14px);
          padding-left: rem(20px); } }
      i {
        position: absolute;
        left: -5px;
        top: 50%;
        @include transform(translateY(-50%));
        font-size: rem(24px);
        @extend %text--base;
        @include media(767px) {
          font-size: rem(20px); } } }
    .btn-area {
      width: 112px;
      @include media(520px) {
        width: 100%;
        margin-top: rem(15px); }
      button {
        @include media(520px) {
          width: 100%; } } } } }
/* hero section css end */


/* inner hero css start */
.inner-hero {
  padding-top: rem(65px);
  padding-bottom: rem(65px);
  position: relative;
  z-index: 1;
  &::after {
    @include positionTwo;
    @extend %base-color-two;
    opacity: 0.65;
    z-index: -1; } }

.page-breadcrumb {
  @include d-flex;
  margin-top: rem(5px);
  li {
    color: rgba(#ffffff, 0.8);
    text-transform: capitalize;
    &::after {
      content: '-';
      color: #ffffff;
      margin: 0 rem(5px); }
    &:first-child {
      &::before {
        content: "\f015";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $base-color;
        margin-right: rem(6px); } }
    &:last-child {
      &::after {
        display: none; } }
    a {
      color: #ffffff;
      text-transform: capitalize;
      &:hover {
        color: $base-color; } } } }
/* inner hero css end */
