/* avaiable blood section css start */
.top-el-bg {
  position: absolute;
  top: -50px;
  z-index: -1;
  opacity: 0.15; }
.avaiable-blood-single {
  text-align: center;
  @extend %base-color-two;
  padding: rem(30px) rem(15px);
  @extend %bs-8;
  position: relative;
  overflow: hidden;
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 15px rgba($base-color-two, 0.35);
    @include transform(translateY(-5px));
    &::after {} }

  &::after {
    position: absolute;
    content: "\f043";
    bottom: -15px;
    right: -25px;
    font-family: 'Line Awesome Free';
    font-weight: 900;
    @extend %text--base;
    font-size: rem(90px);
    opacity: 0.3;
    line-height: 1; }
  &__name {
    @extend %text--base;
    font-size: rem(36px); }
  &__amount {
    @extend %text--white;
    &::before {
      content: '('; }
    &::after {
      content: ')'; } } }
/* avaiable blood section css end */

/* about section css start */
.about-thumb {
  height: 100%;
  position: relative;
  .play-btn {
    position: absolute;
    bottom: -20px;
    right: -20px;
    width: 130px;
    height: 140px;
    @extend %base-color;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    @extend %text--white;
    font-size: rem(56px);
    @include media(991px) {
      right: 0; }
    @include media(480px) {
      width: 100px;
      height: 110px;
      font-size: rem(48px); }
    @include media(380px) {
      width: 80px;
      height: 90px;
      font-size: rem(36px); } }
  img {
    @extend %obj-fit; } }

.about-item {
  @extend %d-flex;
  $root: &;
  @include media(575px) {
    justify-content: center;
    text-align: center;
    padding: rem(20px);
    background-color: #fff;
    @include border-radius(8px);
    box-shadow: 0 5px 15px rgba(#000, 0.1); }
  &__icon {
    width: 65px;
    height: 65px;
    @extend %base-color-two;
    @extend %text--base;
    @extend %bs-5;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: rem(32px);
    margin-top: rem(6px); }
  &__content {
    width: calc(100% - 65px);
    padding-left: rem(25px);
    @include media(575px) {
      width: 100%;
      padding-left: 0;
      margin-top: rem(25px); } }
  &__title {
    margin-bottom: rem(10px); }
  &.style--two {
    @include media(1399px) {
      padding: rem(20px);
      background-color: #fff;
      @include border-radius(8px);
      box-shadow: 0 5px 15px rgba(#000, 0.1);
      justify-content: center;
      text-align: center; }
    #{$root}__icon {
      width: 75px;
      height: 75px;
      font-size: rem(42px); }
    #{$root}__content {
      width: calc(100% - 75px);
      @include media(1399px) {
        width: 100%;
        padding-left: 0;
        margin-top: rem(25px); } } } }

/* about section css end */

/* volunteer section css start */
.volunteer-card {
  padding: rem(15px);
  @extend %bs-8;
  @extend %bg--white;
  box-shadow: 0 3px 6px rgba(#000, 0.1);
  &__thumb {
    height: 280px;
    overflow: hidden;
    @extend %bs-5;
    img {
      width: 100%;
      height: 100%;
      @extend %obj-fit;
      object-position: top;
      -o-object-position: top; } }
  &__content {
    text-align: center;
    padding-top: rem(15px); }
  &__social-links {
    @extend %d-flex;
    justify-content: center;
    margin: rem(-3px) rem(-5px);
    li {
      padding: rem(3px) rem(5px);
      a {
        color: $para-color;
        font-size: rem(18px); } } } }

.volunteer-slider {
  .slick-list {
    margin: rem(-10px); }
  .single-slide {
    padding: rem(10px); } }
/* volunteer section css end */


/* donor list section css start */
.shade--bg2 {
  background: $base-color;
  background: -webkit-linear-gradient(to top, rgba($base-color, 0.15), #fff);
  background: linear-gradient(to top, rgba($base-color, 0.15), #fff); }

.bottom-el-bg {
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0.05;
  z-index: -1;
  height: 100%;
  width: 100%; }

.donor-card {
  @extend %d-flex;
  padding: rem(15px);
  align-items: center;
  @extend %bs-8;
  box-shadow: 0 3px 5px rgba($base-color-two, 0.25);
  position: relative;
  z-index: 1;
  overflow: hidden;
  @extend %base-color;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 105px;
    height: 100%;
    @extend %base-color-two;
    opacity: 1;
    z-index: -1; }
  &__thumb {
    width: 75px;
    height: 75px;
    @include border-radius(50%);
    overflow: hidden;
    img {
      width: inherit;
      height: inherit;
      @extend %obj-fit; } }
  &__content {
    width: calc(100% - 75px);
    padding-left: rem(35px); }
  &__name {
    font-size: rem(18px);
    color: #fff; }
  &__amount {
    font-size: rem(18px);
    color: #fff;
    display: flex;
    align-items: center;
    i {
      font-size: rem(22px); } } }
.donor-slider {
  .slick-list {
    margin: rem(-10px); }
  .single-slide {
    padding: rem(10px); } }
/* donor list section css end */

/* involve section css start */
.involve-item {
  position: relative;
  height: 100%;
  @extend %bs-8;
  overflow: hidden;
  &::before {
    @include positionTwo;
    background: #bdc3c7;
    background: -webkit-linear-gradient(to top, rgba($base-color-two, 1), rgba($base-color-two, 0.05));
    background: linear-gradient(to top, rgba($base-color-two, 1), rgba($base-color-two, 0.05)); }
  &__thumb {
    height: 100%;
    @include media(1199px) {
      height: 300px; }
    @include media(767px) {
      height: 250px; }
    img {
      width: 100%;
      height: 100%;
      @extend %obj-fit; } }
  &__content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: rem(30px);
    p {
      color: darken(#fff, 5%); } }
  &__title {
    color: #fff; } }
/* involve section css end */

/* overview section css start */
.overview-item {
  &__icon {
    font-size: rem(56px);
    color: $base-color;
    line-height: 1;
    @include media(575px) {
      font-size: rem(48px); } }
  &__caption {
    color: #fff;
    font-size: rem(20px);
    margin-top: rem(10px);
    @include media(575px) {
      font-size: rem(18px); }
    @include media(420px) {
      font-size: rem(16px); } } }

.shade--bg {
  background: $base-color;
  background: -webkit-linear-gradient(to bottom, rgba($base-color, 0.15), #fff);
  background: linear-gradient(to bottom, rgba($base-color, 0.15), #fff); }
/* overview section css end */

/* how it work section css start */
.work-item {
  text-align: center;
  &__icon {
    width: 105px;
    height: 105px;
    @extend %base-color-two;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @include border-radius(50%);
    position: relative;
    .step {
      position: absolute;
      width: 30px;
      height: 30px;
      top: 0;
      right: 0;
      @extend %base-color;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: rem(12px);
      @include border-radius(50%); }
    i {
      font-size: rem(56px);
      line-height: 1;
      @extend %text--base; } }
  &__title {
    margin-top: rem(15px);
    @include media(1199px) {
      font-size: rem(18px); } } }
/* how it work section css end */


/* testimonial section css start */
.testimonial-card {
  padding: rem(25px);
  @extend %base-color-two;
  @extend %bs-8;
  &__content {
    color: darken(#fff, 10%);
    .name {
      color: #fff; } } }
.ratings {
  i {
    color: #FAC42D; } }

.testimonial-slider {
  .slick-list {
    margin: rem(-15px); }
  .single-slide {
    padding: rem(15px); } }
/* testimonial section css end */


/* partner section css start */
.partner-item {
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    display: inline-block;
    max-height: 110px;
    @include media(1199px) {
      max-height: 70px; } } }
/* partner section css end */


/* blog section css start */
.blog-post {
  padding: rem(15px);
  @extend %bg--white;
  @extend %bs-8;
  box-shadow: 0 5px 15px rgba(#000, 0.08);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 25px rgba(#000, 0.1); }
  &:hover & {
    &__thumb {
      img {
        @include transform(scale(1.1)); } } }
  &__thumb {
    height: rem(255px);
    @extend %bs-5;
    overflow: hidden;
    a {
      width: 100%;
      height: 100%; }
    img {
      @extend %transition;
      width: 100%;
      height: 100%;
      @extend %obj-fit; } }
  &__meta {
    @extend %d-flex;
    justify-content: space-between;
    align-items: center;
    li {
      font-size: rem(14px); }
    .tag {
      padding: rem(1px) rem(10px);
      @extend %base-color;
      @extend %text--white;
      font-size: rem(13px);
      @extend %bs-5; } }
  &__content {
    padding: rem(20px) rem(10px) rem(5px) rem(10px); }
  &__title {
    a {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2; } } }
/* blog section css end */


/* blog details section css start */
.blog-details-title {
  font-size: rem(48px);
  font-weight: 700;
  @include media(1199px) {
    font-size: rem(42px); }
  @include media(575px) {
    font-size: rem(32px); } }
.tags {
  .single-tag {
    padding: rem(1px) rem(15px);
    background-color: rgba($base-color, 0.1);
    @extend %text--base;
    font-size: rem(13px);
    @extend %bs-5; } }
.blog-details-thumb {
  max-height: 650px;
  overflow: hidden;
  @extend %bs-8; }

.post-share-sidebar {
  text-align: center;
  position: sticky;
  top: 30px;
  @include media(767px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: rem(15px); } }
.share-links {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  @include media(767px) {
    flex-flow: row;
    padding-left: rem(10px); }
  li {
    margin: rem(5px) rem(3px);
    a {
      width: 40px;
      height: 40px;
      color: $heading-color;
      @include border-radius(50%);
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #afafaf;
      font-size: rem(22px); } } }

.blog-details-content {
  p {
    margin-top: 20px;
    &:first-child {
      margin-top: 0; } } }
.blog-details-subtitle {
  font-size: rem(22px);
  font-weight: 500; }
/* blog details section css end */
