/* donor search area css start */
.donor-search-area {
  background-color: darken($base-color-two, 2%);
  padding: rem(20px) 0;
  label {
    color: #fff; } }
.donor-search-form {
  @extend %d-flex;
  align-items: flex-end;
  justify-content: space-between;
  &__field {
    width: calc((100% / 4) - 40px);
    @include media(991px) {
      width: calc((100% / 2) - 10px);
      margin-bottom: rem(15px); }
    @include media(440px) {
      width: 100%; } }
  &__btn {
    width: 100px;
    height: 50px;
    @include media(991px) {
      width: 100%;
      text-align: right; } } }
/* donor search area css end */


/* search donor section css start */
.donor-item {
  padding: rem(10px);
  @extend %bg--white;
  @extend %bs-5;
  @extend %d-flex;
  box-shadow: 0 0 10px rgba(#000, 0.1);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 15px rgba(#000, 0.08);
    @include transform(translateY(-5px)); }
  &__name {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1; }
  &__thumb {
    width: 35%;
    height: 120px;
    @extend %bs-5;
    overflow: hidden;
    @media (min-width: 576px) and (max-width: 767px) {
      width: 100%;
      height: 200px; }
    @media (max-width: 420px) {
      width: 100%;
      height: 200px; } }
  &__content {
    width: 65%;
    padding-left: rem(25px);
    @media (min-width: 576px) and (max-width: 767px) {
      width: 100%;
      padding-left: 0;
      padding: rem(20px) rem(10px) rem(10px) rem(10px); }
    @media (max-width: 420px) {
      width: 100%;
      padding-left: 0;
      padding: rem(20px) rem(10px) rem(10px) rem(10px); } } }
/* search donor section css end */
