/* footer section css start */
.footer {
  li, span, a, i, p {
    @extend %text--white; }
  &__top {
    padding-top: rem(80px);
    padding-bottom: rem(80px); }
  &__bottom {
    padding: rem(20px) 0;
    border-top: 1px solid rgba(#fff, 0.15);
    position: relative;
    z-index: 1;
    &::before {
      @include positionTwo;
      background-color: $base-color-two;
      opacity: 0.45;
      z-index: -1; } }

  .footer-info-area {
    padding-bottom: rem(30px);
    border-bottom: 1px solid rgba(#fff, 0.15);
    margin-bottom: rem(50px); }

  .footer-contact-list {
    @extend %d-flex;
    align-items: center;
    margin: rem(-8px) rem(-15px);
    li {
      @extend %d-flex;
      align-items: center;
      padding: rem(8px) rem(15px);
      .icon {
        width: 75px;
        @include media (575px) {
          width: 55px; }
        i {
          @extend %text--base;
          font-size: rem(42px);
          line-height: 1;
          @include media (575px) {
            font-size: rem(36px); } } }
      .content {
        width: calc(100% - 75px);
        @include media (575px) {
          width: calc(100% - 55px); }
        .title {
          @extend %text--white;
          font-size: rem(17px); }
        a {
          @extend %text--white;
          font-size: rem(20px);
          text-decoration: underline;
          @include media (575px) {
            font-size: rem(16px); } } } } } }

.footer-logo {
  img {
    max-width: rem(175px);
    max-height: rem(60px);
    @include media(1199px) {
      max-width: rem(150px); } } }

.footer-widget {
  &__title {
    @extend %text--white;
    font-size: rem(20px);
    margin-bottom: rem(30px);
    padding-bottom: rem(10px);
    position: relative;
    @include media(991px) {
      margin-bottom: rem(20px); }
    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      @extend %bg--white;
      opacity: 0.25; }
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 35px;
      height: 3px;
      @extend %base-color; } } }

.footer-links-list {
  li {
    padding: rem(6px) 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    a {
      position: relative;
      font-size: rem(14px);
      padding-left: rem(25px);
      &:hover {
        @extend %text--base; }
      &::before {
        position: absolute;
        content: "\f138";
        font-family: 'Line Awesome Free';
        font-weight: 900;
        left: 0;
        top: 0; } } } }

.footer-overview-list {
  @include media(575px) {
    display: flex;
    align-items: center; } }


.footer-overview {
  padding: rem(15px) 0;
  border-bottom: 1px solid rgba(#fff, 0.15);
  &:first-child {
    padding-top: 0; }
  &:last-child {
    padding-bottom: 0;
    border-bottom: none;
    @include media(575px) {
      border-right: none; } }
  @include media(575px) {
    width: 50%;
    text-align: center;
    padding: 0 15px;
    border-bottom: none;
    border-right:  1px solid rgba(#fff, 0.15); }
  &__number {
    font-size: rem(36px);
    @extend %text--white;
    line-height: 1;
    @include media(767px) {
      font-size: rem(28px); } }
  &__caption {
    margin-top: rem(5px); } }

/* footer section css end */
