/* contact section css start */
.contact-bottom-el {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  opacity: 0.15;
  z-index: -1; }

.map-area {
  filter: grayscale(100%);
  iframe {
    width: 100%;
    height: 650px; } }

.single-info {
  $icon: rem(85px);
  &__icon {
    width: $icon;
    height: $icon;
    font-size: calc(#{$icon} / 2);
    @include media(991px) {
      width: rem(75px);
      height: rem(75px); }
    @include media(380px) {
      width: rem(60px);
      height: rem(60px);
      font-size: calc(#{rem(60px)} / 2); } }
  &__content {
    width: calc(100% - #{$icon});
    padding-left: rem(25px);
    .title {
      font-size: rem(28px);
      @include media(991px) {
        font-size: rem(24px); }
      @include media(380px) {
        font-size: rem(22px); } }
    a {
      color: $para-color;
      &:hover {
        @extend %text--base; } } } }
.contact-info-list {
  .single-info {
    padding: rem(20px) 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; } } }

.contact-form {
  box-shadow: 0 5px 25px rgba($base-color-two, 0.35);
  label {
    color: #fff; }
  .form--control, .select {
    background-color: transparent;
    border: 1px solid rgba(#fff, 0.35);
    @include placeholder(rgba(#fff, 0.65));
    color: #fff; }
  .select {
    option {
      @extend %base-color-two; } } }
/* contact section css end */
